
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


import $ from "jquery";

export default {
name: "shopBag",
components: {
ViewBox,
XHeader,
CheckIcon,
Flexbox,
FlexboxItem,
InlineXNumber,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XButton,
Loading
},
data() {
return {
bag_list: [],
totalMoney: 0,
foxBottom: 0,
isAllselect: false,
release_confirm: '',
show_edit: true,
show_complete: false,
to_pay: true,
all_del: false
};
},
methods: {

toEdit() {
this.show_edit = false;
this.show_complete = true;

this.to_pay = false;
this.all_del = true;
},

toComplete() {
this.show_edit = true;
this.show_complete = false;

this.to_pay = true;
this.all_del = false;
},

toAllDel() {
var list = this.getSelectItem();
if(list.length <= 0) {

this.$vux.toast.show({
text: "请选择要删除的商品！",
type: "text",
position: "middle",
width: "12em"
});
return;
}
var order = {
goods_list: list
};
for(var i = 0; i <= order.goods_list.length; i++) {
console.log("要删除的", order.goods_list[i].goods_id);
}
},
buildList(list) {
for(let i = 0; i < list.length; i++) {
list[i].isActive = false;
list[i].goods_count = parseInt(list[i].goods_count);
}
this.bag_list = list;
console.log(this.bag_list)
},
toGoodsInfo(item) {
this.$router.push({
path: "/goodsInfo/goodsInfo",
name: "/goodsInfo/goodsInfo",
params: {
goods: item
}
});
},
goods_countChange(item) {
console.log(item);
this.$post(
"bag/save", {
where: {
bag_id: item.bag_id
},
save: {
goods_count: item.goods_count
}
},
res => {
console.log(res);
if(res.res < 0) {
this.$vux.toast.show({
text: "修改失败,请重试",
type: "text",
position: "middle"
});
}
}
);
},
goToPay() {
var list = this.getSelectItem();
if(list.length <= 0) {

this.$vux.toast.show({
text: "请选择要结算的商品！",
type: "text",
position: "middle",
width: "12em"
});
return;
}
var order = {
goods_list: list
};
this.$router.push({
path: "/postOrder",
name: "/postOrder",
params: {
order: order
}
});
},

getSelectItem() {
var list = [];
for(let i = 0; i < this.bag_list.length; i++) {
let item = this.bag_list[i];
if(item.isActive) {
list.push(item);
}
}
return list;
},

del(item, index, list) {
this.$post("bag/del", {
bag_id: item.bag_id
},
res => {
console.log(res);
if(res.res >= 1) {
list.splice(index, 1);
}
if(res.res < 0) {

}
})
},

toRelease() {
var list = this.getSelectItem();
if(list.length <= 0) {

this.$vux.toast.show({
text: "请选择要发布的商品！",
type: "text",
position: "middle",
width: "12em"
});
return;
}
var order = {
goods_list: list
};
this.$router.push({
path: "/release/release",
name: "/release/release",
params: {
openRelease: '5',
order: order
}
});
}
},
watch: {
isAllselect() {
for(let i = 0; i < this.bag_list.length; i++) {
this.bag_list[i].isActive = this.isAllselect;
}
}
},
computed: {
total() {
var money = 0;
var list = this.getSelectItem();
for(let i = 0; i < list.length; i++) {
let item = list[i];

money += item.user_spec.money * item.goods_count;
}
money = money.toFixed(2);
this.totalMoney = money;
return money;
}
},
mounted() {

this.$post(
"bag/getList", {
page: this.currentPage,
limit: this.pageSize
},
res => {
if(res.res >= 1) {
this.buildList(res.msg)
}
}
);

if(this.$route.params["isGoodsInfo"]) {
this.foxBottom = 0;
} else {
this.foxBottom = 50;
}

if(this.$route.params["openShopBag"] == "1") {
this.release_confirm = "确认"
}
}
};



